body {
	font-family: 	Lato;
	height:			100%;
	width:			100%;
}

h1, h2, h3, h4, h5, h6 {
	font-weight: 700;
	font-family: 'Montserrat';
}

hr[class*="dark"], hr[class*="light"] {
	border: 	1px solid;
	max-width: 	30rem;
	padding:	0;
	margin: 	2.5rem auto;
	text-align: center;
}

hr[class*="dark"]::after, hr[class*="light"]::after {
	display:		inline-block;
	position: 		relative;
	top:			-.75em;
	padding: 		0 .5rem;
	font-family:	'Material Icons';
	font-size: 		3rem;
	text-align:		center;
}

hr[class*="light"] { border-color: #FFFFF0; }
hr[class*="light"]::after {
	color: 				#FFFFF0;
	background-color: 	#303037;
}

hr[class*="dark"] { border-color: #303037; }
hr[class*="dark"]::after {
	color: 				#303037;
	background-color: 	#FFFFF0;
}

/* star: e838, star_border: e83a, stars: e8d0 */
hr[class*="star"]::after 	{ content: '\e83a'; }
hr[class*="obj"]::after 	{ content: '\e869'; }
hr[class*="code"]::after 	{ content: '\e86f'; }
hr[class*="school"]::after 	{ content: '\e80c'; }
hr[class*="subject"]::after { content: '\e8d2'; }
hr[class*="work"]::after 	{ content: '\e8f9'; }
hr[class*="hot"]::after 	{ content: '\e80e'; }
hr[class*="poly"]::after 	{ content: '\e8ab'; }
hr[class*="music"]::after 	{ content: '\e063'; }
hr[class*="game"]::after 	{ content: '\e021'; }
hr[class*="router"]::after 	{ content: '\e328'; }
hr[class*="doc"]::after 	{ content: '\e548'; }
hr[class*="device"]::after 	{ content: '\e1b1'; }

.scroll-to-top {
	display: 	none;
	z-index: 	1042;
	right:		1rem;
	bottom: 	1rem;
}

.scroll-to-top a {
	width: 				3.5rem;
	height: 			3.5rem;
	text-align: 		center;
	line-height: 		4.5rem;
	color: 				#F8F3F2;
	background-color: 	#303037;
	border:				1px solid rgba(128, 128, 128, 0.5);
	border-radius: 		.5rem!important;
	box-shadow:			0px 2px 2px 2px rgba(0, 0, 0, 0.3);
}

.App {
	background: 		linear-gradient(-45deg, #F8F3F2, #B7B0A8, #CB462B, #DBB43D, #303037);
	background-size: 	400% 400%;
	-webkit-animation:	Gradient 15s ease infinite;
	-moz-animation:		Gradient 15s ease infinite;
	animation:			Gradient 15s ease infinite;
}

.App-logo {
	animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes Gradient {
	0% 		{ background-position: 0% 50% }
	50% 	{ background-position: 100% 50% }
	100% 	{ background-position: 0% 50% }
}

@-moz-keyframes Gradient {
	0% 		{ background-position: 0% 50% }
	50% 	{ background-position: 100% 50% }
	100% 	{ background-position: 0% 50% }
}

@keyframes Gradient {
	0% 		{ background-position: 0% 50% }
	50% 	{ background-position: 100% 50% }
	100% 	{ background-position: 0% 50% }
}
